* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  height: 100%;
}

.top {
  justify-content: space-between;
}

header {
  background-color: rgb(221, 215, 215);
  padding: 5px 20px;
  height: 60px;
}

header a{
  padding: 3px;
}

header a:hover{
  border-radius: 4px;
  border-bottom: 4px solid black;
  background-color: aqua;
}

header svg {
  margin: 4px;
  font-size: 22px;
}

header p {
  margin: 1px;
}

.logo {
  width: 120px;
  height: 45px;
}

.social {
  margin: 0px 10px;
}
.social a {
  margin: 0px 6px;
}

.nav-item {
  margin: 8px 12px;
}

.nav-link:hover{
  /* text-decoration: underline; */
  color: burlywood !important;
  /* line-height: 25px; */
  /* letter-spacing: 1px; */
  font-family: ubuntu;
  font-weight: 600;
  border-bottom: 2px solid white;
}

.C-img {
  height: 450px;
}

.mainh2 {
  font-family: sans-serif;
  font-size: 25px;
  color: #644141;
  text-align: center;
  margin: 50px 0px;
  margin-bottom: 6px;
  letter-spacing: 0.1px;
}

.mainp {
  font-family: auto;
  font-size: 18px;
  font-weight: 500;
  width: 86vw;
  margin: 5px auto;
  text-align: center;
}

.mainCards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  width: 21rem;
  height: 90%;
  margin: 20px 12px;
  padding: 10px;
  text-align: center;
}

.card:hover{
  cursor: pointer;
  background: transparent !important;
  border: 3px solid grey;
  box-sizing: border-box;
  box-shadow: -1px 5px 12px 4px #6a705f;
}

.card-btn:hover{
  background: transparent;
  border: 1.5px solid rgb(107, 101, 101);
  color: black;
  font-weight: 600;
}

.card img {
  margin: 14px 16px;
}

.card-body h5 {
  font-family: monospace;
  line-height: 24px;
  letter-spacing: 0.3px;
  font-weight: 600;
}

.card p {
  font-family: "Ubuntu", sans-serif;
  font-size: 15px;
  width: 90%;
  margin: 20px auto;
}

.num-section{
  margin: 30px 0px;
}

.num-img{
  position: relative;
  top: 50px;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 200px;
  opacity: 0.45;
  filter: brightness(15%);
  filter: contrast(20%);
  filter: saturate(10);
}

.numbers {
  position: relative;
  top: -6rem;
  left: 0;
  margin: 0px 0vw; 
  padding: 15px 12vw;
  justify-content: space-between;
  filter: saturate(5);
  filter: brightness(100%);
  filter: contrast(100%);
}

.number-item h4 {
  font-size: 26px;
  font-family: cursive;
  font-weight: bolder;
  margin-bottom: 0px;
  text-align: center;
}

.number-item p {
  font-size: 21px;
  font-weight: 600;
} 

.contact form{
  width: 80%;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  height: fit-content;
  background-color: #8eb9f6;
  padding: 20px 5vw;
  border-radius: 15px;
  margin: auto;
}

.contact h4{
  font-size: 25px;
  font-family: ubuntu;
  font-weight: 500;
  text-align: center;
  color: black;
  margin: 30px 0px;
}

.contact input{
  width: 28vw;
  margin: 15px;
  padding: 12px 20px;
  font-family: ubuntu;
  font-size: 17px;
  font-weight: 500;
}

.contact textarea{
  width: 63vw;
  margin: 15px;
  padding: 12px 20px;
  font-family: ubuntu;
  font-size: 17px;
  font-weight: 500;
}

.contact-btn:hover{
  background-color: green !important;
  color: purple !important;
}

.footer{
  background-color: rgb(46, 41, 41);
  color: white;
  margin-top: 80px;
  margin-bottom: 0px;
  padding: 50px 10px;
  padding-bottom : 3px;
}

footer{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 20px 13px;
}

.footer-section{
  width: 22%;
  margin: 25px 10px;
}
.footer-section h5{
  font-family: ubuntu;
  font-weight: 800;
}
.footer-section a{
  text-decoration: none;
  color: white;
  font-size: 14px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.footer-section a:hover{
  color: #9c9e98;
}

.last{
  display: flex;
  justify-content: space-around;
  text-align: center;
  align-items: center;
  margin: 40px 0px;
  padding: 0px 13vw;
  color: white;
}

.last svg{
  background-color: rgb(255, 255, 255); 
  font-size: 30px;
  border-radius: 8px;
}

.costumer-support{
  width: 40vw;
  text-align: center;
}